@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@200&display=swap');
.Body{
    margin-top: 2%;
    display: flex;
    flex-direction: column;
    gap: 20px;
    
}

.Heading{
    width: 100%;
    text-align: center;
    font-family: 'Roboto Mono', monospace;
    font-size: 30px;
    font-weight: 650;
    color: aliceblue;
    font-size: 30px;
    text-shadow: 1px 1px 2px rgb(0, 0, 0), 0 0 25px blue, 0 0 5px darkblue;
}

.SubHeading{
    width: 100%;
    text-align: center;
    font-size: 22px;
    text-shadow: 1px 1px 1px #401996;
}

.Blogcards{
    display: flex;
    flex-direction: row;
    gap: 10px;
}

@media screen and (max-width: 600px) {
    .Blogcards {
        display: flex;
        flex-direction: column;
        height: 100%;
        width: 100%;
        justify-content: center;
        align-items: center;
    }
}